//vuex状态管理
import Vue from 'vue';
import Vuex from 'vuex';
import mutations from './mutations.js';
import actions from './actions.js';

//全局注册
Vue.use(Vuex);

//创建store对象
const store = new Vuex.Store({
    //状态数据
    state: {
        //主题分类数据
        topics: [
            { name: 'all', description: '全部' },
            { name: 'good', description: '精华' },
            { name: 'share', description: '分享' },
            { name: 'ask', description: '问答' },
            { name: 'job', description: '招聘' },
            { name: 'dev', description: '测试' }
        ],
        //未登录更多菜单
        showMenus: false,
        //登录状态
        isLogined: false,
        //登录用户名
        loginname: '',
        //登录用户的accesstoken
        accesstoken: '',
        //登录用户id
        author_id: '',
        //未登录状态 显示的菜单
        unLoginedmoreMenus: {
            login: '登录'
        },
        loginedMoreMenus: {
            create: '发表新话题',
            center: '个人中心',
            msg: '消息通知',
            logout: '退出'
        }
    },
    //状态数据的计算属性
    getters: {
        moreMenus: function (state) {
            if (state.isLogined == true) {
                //用户处理登录状态，返回登录后的更多菜单
                return state.loginedMoreMenus;
            }
            return state.unLoginedmoreMenus;
        },
        getTopicDescByTab: function(state){
            //需要返回一个函数结构
            return function(tab){
                for(var index = 0; index < state.topics.length; index ++ ){
                    if(state.topics[index].name == tab){
                        return state.topics[index].description;
                    }
                }
            }
        }
    },
    mutations,
    actions
});

//对外抛出
export default store;


